export const RouterRoutes = [
  {
    path: "/:lang?",
    Component: LanguageMiddleware,
    entry: "@/router/middleware/language.jsx",
    children: [
      {
        path: "",
        Component: DefaultLayout,
        entry: "@/layout/default/index.jsx",
        children: [
          {
            path: "",
            Component: AppErrorBoundary,
            entry: "@/components/appErrorBoundary.jsx",
            children: [
              {
                path: "",
                index: true,
                Component: HomePage,
                entry: "@/pages/homePage/index.jsx",
              },
              {
                path: "*",
                Component: Error404,
                entry: "@/pages/errors/error404.jsx",
              },
              import.meta.env.MODE === "development"
                ? {
                    path: "style-guide",
                    Component: StyleGuidePage,
                    entry: "@/pages/styleGuide/index.jsx",
                  }
                : undefined,
            ],
          },
        ],
      },
    ],
  },
];

export default RouterRoutes;
